<template>
  <div class="row borderTop">
    <div class="stack">
      <h3>{{ $t("Global.techs") }}</h3>
      <div v-for="(j, i) in stack" :key="i">
        {{ j.name }}
        <div class="bar">
          <div class="barIn" :style="`width: ${j.value}%;`">{{ j.value }}%</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import data from "../../public/stack.json";

export default {
  name: "Stack",
  data() {
    return {
      stack: data,
    };
  },
};
</script>

<style scoped>
.borderTop {
  margin-top: 0.5rem;
}

.stack {
  margin-left: 3%;
  margin-top: 0.5rem;
}

.bar {
  width: 100%;
  height: 1rem;
  border: 2px solid silver;
  border-radius: 0.5rem;
  font-size: 0.5rem;
  overflow: hidden;
}

@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

.barIn {
  padding-right: 1rem;
  border-radius: 0.4rem;
  background-color: #128640;
  color: white;
